const months = {
  1: "January",
  2: "February",
  3: "March",
  4: "April",
  5: "May",
  6: "June",
  7: "July",
  8: "August",
  9: "September",
  10: "October",
  11: "November",
  12: "December",

}


export const formatDate = (date) => {
  if(date == 'now' || date == 'actualidad') return 'now';
  let dlist = date.split('-');
  return months[Number(dlist[1])].substring(0,3) + " " + dlist[0];
}



